.section__body{
    padding: 20px;
}

.form__button{
    background-color: hsl(180, 100%, 25%);
    color: white;
    padding: 10px;
    border:none;
}

.form__button:hover{
    background-color: hsl(180, 100%, 30%);
}
.form__button:active{
    background-color: hsl(180, 100%, 35%);
}

.form{
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

@media (max-width: 767px){
    .form{
        grid-template-columns: 1fr;
    }
}

.form__input{
    padding:10px;
    border: 1px solid #ddd;
}

